import styles from "./RegistrationBlock.module.css"
import { useState, useRef } from "react"
import ExitArrow from "../Generic/ExitArrow"
import Cross from "../Generic/Cross"

const RegistrationBlock = (props) => {
  const [visibleOneInput, setVisibleOneInput] = useState(true)
  const [data, setData] = useState({})

  const [stateInput1, setStateInput1] = useState(true)
  const [stateInput2, setStateInput2] = useState(true)
  const [stateInput3, setStateInput3] = useState(true)
  const [stateInput4, setStateInput4] = useState(true)
  const [stateInput5, setStateInput5] = useState(true)
  const [stateInput6, setStateInput6] = useState(true)
  const [stateInput7, setStateInput7] = useState(true)
  const [stateInput8, setStateInput8] = useState(true)
  const [stateInput9, setStateInput9] = useState(true)

  // считывание значений input
  const nameRef = useRef(null)
  const surnameRef = useRef(null)
  const telephoneRef = useRef(null)
  const cityRef = useRef(null)
  const frontDoorRef = useRef(null)
  const streetRef = useRef(null)
  const floorRef = useRef(null)
  const houseRef = useRef(null)
  const apartmentRef = useRef(null)

  const closeOneInput = () => {
    if (nameRef.current.value == "") {
      setStateInput1(false)
    }
    if (surnameRef.current.value == "") {
      setStateInput2(false)
    }
    if (telephoneRef.current.value == "") {
      setStateInput3(false)
    }

    if (
      data.name != undefined &&
      data.surname != undefined &&
      data.telephone != undefined
    ) {
      setVisibleOneInput(false)
    }
  }
  const closeRegistrationBlock = () => {
    props.onClose()
    props.closeRegistrationBlock()
  }

  // добавление в data
  const handleNameInputChange = () => {
    nameRef.current.value == "" ? setStateInput1(false) : setStateInput1(true)
    const data1 = {
      name: nameRef.current.value,
    }
    setData({ ...data, ...data1 })
  }
  const handleSurnameInputChange = () => {
    surnameRef.current.value == ""
      ? setStateInput2(false)
      : setStateInput2(true)
    const data2 = {
      surname: surnameRef.current.value,
    }
    setData({ ...data, ...data2 })
  }
  const handleTelephoneInputChange = () => {
    telephoneRef.current.value == ""
      ? setStateInput3(false)
      : setStateInput3(true)
    const data3 = {
      telephone: telephoneRef.current.value,
    }
    setData({ ...data, ...data3 })
  }
  const handleCityInputChange = () => {
    cityRef.current.value == "" ? setStateInput4(false) : setStateInput4(true)
    const data4 = {
      city: cityRef.current.value,
    }
    setData({ ...data, ...data4 })
  }
  const handleFrontDoorInputChange = () => {
    frontDoorRef.current.value == ""
      ? setStateInput5(false)
      : setStateInput5(true)
    const data5 = {
      frontDoor: frontDoorRef.current.value,
    }
    setData({ ...data, ...data5 })
  }
  const handleStreetInputChange = () => {
    streetRef.current.value == "" ? setStateInput6(false) : setStateInput6(true)
    const data6 = {
      street: streetRef.current.value,
    }
    setData({ ...data, ...data6 })
  }
  const handleFloorInputChange = () => {
    floorRef.current.value == "" ? setStateInput7(false) : setStateInput7(true)
    const data7 = {
      floor: floorRef.current.value,
    }
    setData({ ...data, ...data7 })
  }
  const handleHouseInputChange = () => {
    houseRef.current.value == "" ? setStateInput8(false) : setStateInput8(true)
    const data8 = {
      house: houseRef.current.value,
    }
    setData({ ...data, ...data8 })
  }
  const handleApartmentInputChange = () => {
    apartmentRef.current.value == ""
      ? setStateInput9(false)
      : setStateInput9(true)
    const data9 = {
      apartment: apartmentRef.current.value,
    }
    setData({ ...data, ...data9 })
  }

  const saveDataInput = () => {
    if (cityRef.current.value == "") {
      setStateInput4(false)
    }
    if (frontDoorRef.current.value == "") {
      setStateInput5(false)
    }
    if (streetRef.current.value == "") {
      setStateInput6(false)
    }
    if (floorRef.current.value == "") {
      setStateInput7(false)
    }
    if (houseRef.current.value == "") {
      setStateInput8(false)
    }
    if (apartmentRef.current.value == "") {
      setStateInput9(false)
    }

    if (
      data.city != undefined &&
      data.frontDoor != undefined &&
      data.street != undefined &&
      data.floor != undefined &&
      data.house != undefined &&
      data.apartment != undefined
    ) {
      props.registrationData(data)
      closeRegistrationBlock()
    }
  }
  const oneInput = (
    <>
      <div className={styles["RegistrationBlock__wrapper-input"]}>
        <input
          type="text"
          className={
            stateInput1
              ? styles["RegistrationBlock__input"]
              : `${styles["RegistrationBlock__input"]} ${styles["RegistrationBlock__input-noActive"]}`
          }
          ref={nameRef}
          onChange={handleNameInputChange}
          placeholder="Имя"
        />
      </div>
      <div className={styles["RegistrationBlock__wrapper-input"]}>
        <input
          type="text"
          className={
            stateInput2
              ? styles["RegistrationBlock__input"]
              : `${styles["RegistrationBlock__input"]} ${styles["RegistrationBlock__input-noActive"]}`
          }
          ref={surnameRef}
          onChange={handleSurnameInputChange}
          placeholder="Фамилия"
        />
      </div>
      <div className={styles["RegistrationBlock__wrapper-input"]}>
        <input
          type="tel"
          className={
            stateInput3
              ? styles["RegistrationBlock__input"]
              : `${styles["RegistrationBlock__input"]} ${styles["RegistrationBlock__input-noActive"]}`
          }
          ref={telephoneRef}
          onChange={handleTelephoneInputChange}
          placeholder="Телефон"
        />
      </div>
    </>
  )
  const twoInput = (
    <>
      <div className={styles["RegistrationBlock__flex-input"]}>
        <input
          className={
            stateInput4
              ? styles["RegistrationBlock__input-two"]
              : `${styles["RegistrationBlock__input-two"]} ${styles["RegistrationBlock__input-noActive"]}`
          }
          type="text"
          ref={cityRef}
          onChange={handleCityInputChange}
          placeholder="Город"
        />
        <input
          className={
            stateInput5
              ? styles["RegistrationBlock__input-two"]
              : `${styles["RegistrationBlock__input-two"]} ${styles["RegistrationBlock__input-noActive"]}`
          }
          type="number"
          ref={frontDoorRef}
          onChange={handleFrontDoorInputChange}
          placeholder="Подъезд"
        />
      </div>
      <div className={styles["RegistrationBlock__flex-input"]}>
        <input
          className={
            stateInput6
              ? styles["RegistrationBlock__input-two"]
              : `${styles["RegistrationBlock__input-two"]} ${styles["RegistrationBlock__input-noActive"]}`
          }
          type="text"
          ref={streetRef}
          onChange={handleStreetInputChange}
          placeholder="Улица"
        />
        <input
          className={
            stateInput7
              ? styles["RegistrationBlock__input-two"]
              : `${styles["RegistrationBlock__input-two"]} ${styles["RegistrationBlock__input-noActive"]}`
          }
          type="number"
          ref={floorRef}
          onChange={handleFloorInputChange}
          placeholder="Этаж"
        />
      </div>
      <div className={styles["RegistrationBlock__flex-input"]}>
        <input
          className={
            stateInput8
              ? styles["RegistrationBlock__input-two"]
              : `${styles["RegistrationBlock__input-two"]} ${styles["RegistrationBlock__input-noActive"]}`
          }
          type="number"
          ref={houseRef}
          onChange={handleHouseInputChange}
          placeholder="Дом"
        />
        <input
          className={
            stateInput9
              ? styles["RegistrationBlock__input-two"]
              : `${styles["RegistrationBlock__input-two"]} ${styles["RegistrationBlock__input-noActive"]}`
          }
          type="number"
          ref={apartmentRef}
          onChange={handleApartmentInputChange}
          placeholder="Квартира"
        />
      </div>
    </>
  )

  return (
    <>
      <div className={styles["RegistrationBlock__wrapper-blure"]}>
        <ExitArrow exit={closeRegistrationBlock} />
        <div className={styles["RegistrationBlock__wrapper_flex"]}>
          <div className={styles["RegistrationBlock__wrapper"]}>
            <Cross close={closeRegistrationBlock}/>
            <div className={styles["RegistrationBlock__wrapper-text"]}>
              <h1 className={styles["RegistrationBlock__h1"]}>Вход на сайт</h1>
              <p className={styles["RegistrationBlock__p"]}>
                укажите данные, чтобы сделать заказ
              </p>
              {visibleOneInput && oneInput}
              {!visibleOneInput && twoInput}
              <div className={styles["RegistrationBlock__wrapper-line"]}>
                <hr className={styles["RegistrationBlock__line"]}></hr>
              </div>
              {visibleOneInput && (
                <button
                  className={styles["RegistrationBlock__button"]}
                  onClick={closeOneInput}
                >
                  Сохранить
                </button>
              )}
              {!visibleOneInput && (
                <button
                  className={styles["RegistrationBlock__button"]}
                  onClick={saveDataInput}
                >
                  Войти
                </button>
              )}
            </div>
          </div>
        </div>
      </div>
    </>
  )
}
export default RegistrationBlock
