import styles from "./BurgerMenu.module.css"

const BurgerMenu = (props) => {
  const onClick = () => { 
    props.onClick(props)
  }
  return (
    <div className={styles["BurgerMenu__wrapper-card_flex"]}>
      <div className={styles["BurgerMenu__wrapper-card"]}>
        <div className={styles["BurgerMenu__container-img"]}>{props.img}</div>
        <h2 className={styles["BurgerMenu__container-title"]}>{props.title}</h2>
        <div className={styles["BurgerMenu__container_flex"]}>
          <div className={styles["BurgerMenu__wrapper-button"]}><button className={styles["BurgerMenu__button"]} onClick={onClick}>заказать</button></div>
          <div><h2 className={styles["BurgerMenu__h2-color-green"]}>{props.price}</h2></div>
        </div>
      </div>
    </div>
    
  )
}
export default BurgerMenu
