import styles from "./SaladsMenu.module.css"

const SaladsMenu = (props) => {
  const onClick = () => {
    props.onClick(props)
  }
  return ( 
    <div className={styles["SaladsMenu__wrapper-card_flex"]}>
      <div className={styles["SaladsMenu__wrapper-card"]}>
        <div className={styles["SaladsMenu__container-img"]}>{props.img}</div>
        <h2 className={styles["SaladsMenu__container-title"]}>{props.title}</h2>
        <div className={styles["SaladsMenu__container_flex"]}>
          <div className={styles["SaladsMenu__wrapper-button"]}><button className={styles["SaladsMenu__button"]} onClick={onClick}>заказать</button></div>
          <div><h2 className={styles["SaladsMenu__h2-color-green"]}>{props.price}</h2></div>
        </div>
      </div>
    </div> 
  )
}
export default SaladsMenu
