import styles from "./ModulWindow.module.css"
import restaurant from "../../img/restaurant.svg"
import delivery from "../../img/delivery.svg"
import check_tick from "../../img/check-tick.svg"
import ButtonModulWindow from "./ButtonModulWindow"
import { useState } from "react"
import Cross from "../Generic/Cross"


const ModulWindow = (props) => {
  const [restaurantButton, setRestaurantButton] = useState(true)
  const [crossButton, setCrossButton] = useState(false)

  const [restaurantButtonColor, setRestaurantButtonColor] = useState(false)
  const [crossButtonColor, setCrossButtonColor] = useState(false)


  const restaurantButtonHandler = () => {
    setRestaurantButton(true)
    setCrossButton(false)

    setRestaurantButtonColor(true)
    setCrossButtonColor(false)
  }
  const crossButtonHandler = () => {
    setCrossButton(true)
    setRestaurantButton(false)

    setCrossButtonColor(true)
    setRestaurantButtonColor(false)
  }

  const closeCross = () => {
    props.onClick()
  }

  const onClickButtonRestaurant = () => {
    props.visibleRestaurant()
    closeCross() //вызываем функцию удаления окна
    if(props.flag === true){
      props.noVisibleCartPayWindow()
    }

  }
  const onClickCross = () => {
    props.visibleRegistration()
    closeCross() //вызываем функцию удаления окна
  }
  return (
    <div className={styles["ModulWindow__container-wrapper"]}>
      <div className={styles["ModulWindow__wrapper"]}>
        <Cross close={()=>closeCross()}/>
        <h3 className={styles["ModulWindow__title_h3"]}>
          Выберите способ получения заказа
        </h3>
        <p className={styles["ModulWindow__title_p"]}>
          и мы покажем тебе актуальное меню ресторана
        </p>
        <div className={styles["ModulWindow__flex-container"]}>
          <div
            className={
              restaurantButtonColor
                ? styles["ModulWindow__card_active"]
                : styles["ModulWindow__card"]
            }
            onClick={restaurantButtonHandler}
          >
            {restaurantButtonColor && (
              <div className={styles["ModulWindow__container-img_check-tick"]}>
                <img
                  className={styles["ModulWindow__img_check-tick"]}
                  src={check_tick}
                  alt=""
                />
              </div>
            )}
            <img
              className={styles["ModulWindow__img"]}
              src={restaurant}
              alt=""
            />
            <h4 className={styles["ModulWindow__title_h4"]}>В ресторане</h4>
          </div>
          <div
            className={
              crossButtonColor
                ? styles["ModulWindow__card_active"]
                : styles["ModulWindow__card"]
            }
            onClick={crossButtonHandler}
          >
            {crossButtonColor && (
              <div className={styles["ModulWindow__container-img_check-tick"]}>
                <img
                  className={styles["ModulWindow__img_check-tick"]}
                  src={check_tick}
                  alt=""
                />
              </div>
            )}
            <img
              className={styles["ModulWindow__img"]}
              src={delivery}
              alt=""
            ></img>
            <h4 className={styles["ModulWindow__title_h4"]}>Доставка</h4>
          </div>
        </div>
        {restaurantButton && (
          <ButtonModulWindow
            text={"Выбрать ресторан"}
            onClickButton={onClickButtonRestaurant}
          />
        )}
        {crossButton && (
          <ButtonModulWindow
            text={"Необходимо авторизоваться"}
            onClickButton={onClickCross}
          />
        )}
      </div>
    </div>
  )
}
export default ModulWindow
