import { useState } from "react"
import ReactDOM from "react-dom"
import ModulWindow from "./ModulWindow"
import styles from "./Window.module.css"

const Backdrop = (props) => {
  return (
    <div
      id="blurWindow"
      className={styles["Window__wrapper-blure"]}
      onClick={props.onClick}
    ></div>
  )
}
const ModulWindow1 = (props) => {
  return <ModulWindow onClick={props.onClick} visibleRegistration={()=>props.visibleRegistration()} visibleRestaurant={()=>props.visibleRestaurant()} flag={props.flag} noVisibleCartPayWindow={()=>props.noVisibleCartPayWindow()}/>
} 
const Window = (props) => {
  const onClick = () => {
    props.onClose()
  }
  return (
    <>
      {ReactDOM.createPortal(
        <Backdrop onClick={onClick} />,
        document.getElementById("backdrop")
      )}
      {ReactDOM.createPortal(
        <ModulWindow1 onClick={onClick} visibleRegistration={()=>props.visibleRegistration()} visibleRestaurant={()=>props.visibleRestaurant()} flag={props.flag} noVisibleCartPayWindow={()=>props.noVisibleCartPayWindow()}/>,
        document.getElementById("modal")
      )}
    </>
  )
}
export default Window
