import styles from "./RollMenu.module.css"

const RollMenu = (props) => {
  const onClick = () => {
    props.onClick(props)
  }
  return (
    <div className={styles["RollMenu__wrapper-card_flex"]}>
      <div className={styles["RollMenu__wrapper-card"]}>
        <div className={styles["RollMenu__container-img"]}>{props.img}</div>
        <h2 className={styles["RollMenu__container-title"]}>{props.title}</h2>
        <div className={styles["RollMenu__container_flex"]}>
          <div className={styles["RollMenu__wrapper-button"]}><button className={styles["RollMenu__button"]} onClick={onClick}>заказать</button></div>
          <div><h2 className={styles["RollMenu__h2-color-green"]}>{props.price}</h2></div>
        </div>
      </div>
    </div>
    
  )  
}
export default RollMenu
