import { useState } from "react"
import RestaurantAddressCard from "./RestaurantAddressCard"
import styles from "./RestaurantBlock.module.css"
import Cross from "../Generic/Cross"
import RestaurantAddress from "../../img/restaurantAddress.svg"
const RestaurantBlock = (props) => {
  const [buttonActive, setButtonActive] = useState(false)
  const [dataClickCard, setDataClickCard] = useState()

  const addressClick = (e) => {
    props.addressClick(e)
    setButtonActive(true)
    setDataClickCard(e)
  }
  const clickButton = () => {
    if (buttonActive === true) {
      props.dataClickCard(dataClickCard)
    }
  }
  return (
    <div className={styles["RestaurantBlock__background"]}>
      <div className={styles["RestaurantBlock__wrapper_flex"]}>
        <div className={styles["RestaurantBlock__wrapper"]}>
          <div className={styles["RestaurantBlock__wrapper-card"]}>
            {props.addressData.map((e) => (
              <RestaurantAddressCard
                title={e.title}
                number={e.number}
                time={e.time}
                visibleClick={e.visibleClick}
                id={e.id}
                addressClick={addressClick}
                key={e.id}
              />
            ))}
          </div>
          <button
            className={
              buttonActive
                ? styles["RestaurantBlock__button-active"]
                : styles["RestaurantBlock__button"]
            }
            onClick={clickButton}
          >
            Выбрать
          </button>
        </div>
        <div className={styles["RestaurantBlock__container-img-background"]}>
          <div className={styles["RestaurantBlock__container-cross"]}><Cross close={()=>props.onClickCross()}/></div>
          <img className={styles["RestaurantBlock__img-background"]} src={RestaurantAddress} alt=""/>
        </div>
      </div>
    </div>
  )
}
export default RestaurantBlock
