import styles from "./BurgerMenuWindow.module.css"

const BurgerMenuWindow = (props) => {
    return (
        <>
            <div className={styles["BurgerMenuWindow__blur"]} onClick={()=> props.onClose()}></div>
            <div className={styles["BurgerMenuWindow__container"]}>
                <div onClick={()=>props.clickRegistration()}><h2 className={`${styles["BurgerMenuWindow__title"]} ${styles["BurgerMenuWindow__title1"]}`}>Войти</h2></div>
                <hr/>
                <div onClick={()=>props.clickRestaurant()}><h2 className={`${styles["BurgerMenuWindow__title"]} ${styles["BurgerMenuWindow__title2"]}`}>Выбрать адрес</h2></div>
                <hr/>
                <div onClick={()=>props.clickInfo()}><h2 className={`${styles["BurgerMenuWindow__title"]} ${styles["BurgerMenuWindow__title3"]}`}>Информация</h2></div>
            </div>
        </>
        
    )
}
export default BurgerMenuWindow