import styles from "./PotatoMenu.module.css"

const PotatoMenu = (props) => {
  const onClick = () => {
    props.onClick(props)
  } 
  return (
    <div className={styles["PotatoMenu__wrapper-card_flex"]}>
      <div className={styles["PotatoMenu__wrapper-card"]}>
        <div className={styles["PotatoMenu__container-img"]}>{props.img}</div>
        <h2 className={styles["PotatoMenu__container-title"]}>{props.title}</h2>
        <div className={styles["PotatoMenu__container_flex"]}>
          <div className={styles["PotatoMenu__wrapper-button"]}><button className={styles["PotatoMenu__button"]} onClick={onClick}>заказать</button></div>
          <div><h2 className={styles["PotatoMenu__h2-color-green"]}>{props.price}</h2></div>
        </div>
      </div>
    </div> 
  )
}
export default PotatoMenu
