import styles from ".//NewMenu.module.css"
const NewMenu = (props) => {
    const onClick = () => { 
      props.onClick(props) 
    }
    return (
      <div className={styles["NewMenu__wrapper-card_flex"]}>
        <div className={styles["NewMenu__wrapper-card"]}>
          <div className={styles["NewMenu__container-img"]}>{props.img}</div>
          <h2 className={styles["NewMenu__container-title"]}>{props.title}</h2>
          <div className={styles["NewMenu__container_flex"]}>
            <div className={styles["NewMenu__wrapper-button"]}><button className={styles["NewMenu__button"]} onClick={onClick}>заказать</button></div>
            <div><h2 className={styles["NewMenu__h2-color-green"]}>{props.price}</h2></div>
          </div>
        </div>
      </div>
    )
}
export default NewMenu