import { useState } from "react"
import Window from "./Window"
const ModulStart = (props) => {
  const close = () => {
    props.noVisibleStartWindow(false)
  }
  const closeBlure = () => {
    props.noVisibleStartWindow(false)
  }

  const visibleRegistration = ()=>{
    props.visibleRegistration()
  }
  const visibleRestaurant = () => {
    props.visibleRestaurant()
  }
  return (
    <>
      {props.visibleStartWindow && (
        <Window onClose={close} onCloseBlure={closeBlure} visibleRegistration={visibleRegistration} visibleRestaurant={visibleRestaurant} flag={props.flag} noVisibleCartPayWindow={()=>props.noVisibleCartPayWindow()}/>
      )}
    </>
  )
}
export default ModulStart
