import { useRef, useState } from "react"
import styles from "../MainPage/MainPage.module.css"
//* импорт изображений *//
import logo from "../../img/logo.svg"
import userCircle from "../../img/userCircle.svg"
import pencil from "../../img/pencil.svg"
import cart from "../../img/cart.svg"
import MenuBurger from "../../img/burger-menu.svg"
//* импорт компонентов *//
import NewMenu from "../Menu/NewMenu"
import BurgerMenu from "../Menu/BurgerMenu"
import RollMenu from "../Menu/RollMenu"
import PotatoMenu from "../Menu/PotatoMenu"
import SaucesMenu from "../Menu/SaucesMenu"
import BeveragesMenu from "../Menu/BeveragesMenu"
import DessertMenu from "../Menu/DessertMenu"
import SaladsMenu from "../Menu/SaladsMenu"
import AddInCart from "./AddInCart"
import WindowRecherche from "./WindowRecherche"
//* импорт данных компонентов *//
import dataNewMenu from "../Data/DataMenu/DataNewMenu"
import dataBurgerMenu from "../Data/DataMenu/DataBurgerMenu"
import dataRollMenu from "../Data/DataMenu/DataRollMenu"
import dataPotatoMenu from "../Data/DataMenu/DataPotatoMenu"
import dataSaucesMenu from "../Data/DataMenu/DataSaucesMenu"
import dataBeveragesMenu from "../Data/DataMenu/DataBeveragesMenu"
import dataDessertMenu from "../Data/DataMenu/DataDessertMenu"
import dataSaladsMenu from "../Data/DataMenu/DataSaladsMenu"
import dataFullMenu from "../Data/DataMenu/DataFullMenu"
import BurgerMenuWindow from "./BurgerMenuWindow"

let data = []
const MainPage = (props)=>{
    const [pages, setPages] = useState([true, false, false, false, false, false, false, false]);
    const [visibleAddInCart, setVisibleAddInCart] = useState(false)
    const [visibleInputWindow, setVisibleInputWindow] = useState(false)
    const [visibleBurgerMenuWindow, setVisibleBurgerMenuWindow] = useState(false)
    const [stateTitle, setStateTitle] = useState("Еще")
    const [dataCard, setDataCard] = useState() /* временное хранилище данных товара */
    const [dataInput, setDataInput] = useState()
    const inputRef = useRef(null)
    
    const clickAddress = ()=>{
        props.visibleRestaurantClick()
    }
    const clickPencil = () => {
        props.visibleRestaurantClick()
    }
    const onClickRegistration = () => {
        props.onClickRegistration()
    }
    //* МЕНЮ ТОВАРОВ!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!! *//
    const handleClick = (pageIndex) => {
        const newPages = pages.map((page, index) => index === pageIndex);
        setPages(newPages);
    }
    const cartClick = () => {
        props.onCloseMainPage()
        if(props.address === undefined){
            props.flag()
            props.visibleCartWindowPay()
        }
    }
    //* Открывает промежуточное окно перед корзиной заказов *//
    const addProductCard = (e) => {
        setVisibleAddInCart(true)
        setDataCard(e)
    }
    //* Закрытие промежуточного окна помещения товара в корзину *//
    const onClickBlur = () => {
        setVisibleAddInCart(false)
    }
    const FullProductData = (e) => {
        props.FullProductData(e)
    }
    const saveValueInput = () => {
        setVisibleInputWindow(true)
        let valueInput = inputRef.current.value
        data = []
        dataFullMenu.map((e)=>{
            if(e.title.toLowerCase().includes(valueInput.toLowerCase())){
                data.push(e)
            }
            setDataInput(data)
        })
    }
    const AddInCartFromInputWindow = (e)=> {
        setVisibleAddInCart(true)
        setDataCard(e)
    }
    const close = () =>{
        setVisibleInputWindow(false)
        inputRef.current.value = ""
    } 
    const onClickInfo = ()=> {
        props.visibleInfo()
    }
    const clickMoreCategories = () => {
        const element = document.getElementById("categories-block2")
        element.classList.toggle("changed")
        if (element.classList.contains("changed")) {
            // При первом нажатии добавляем стиль "changed"
            element.style.display = "flex"
            element.style.justifyContent = "center"
            setStateTitle("Меньше")
         } else {
            // При повторном нажатии возвращаем исходные стили
            element.style.display = "none"
            setStateTitle("Еще")
         }
    }
    const burgerMenuClick = () => {
        if(visibleBurgerMenuWindow === false){
            setVisibleBurgerMenuWindow(true)
        }
        else{
            setVisibleBurgerMenuWindow(false)
        }
    }
    return (
    <div className={styles["mainPage__wrapper"]}>
        <div className={styles["mainPage__nav_menu"]}>
            <img className={styles["mainPage__logo"]} src={logo} alt="лого бургер"/>
            <h1 className={styles["mainPage__address-title"]} onClick={clickAddress}>{props.dataClickCardButton.title}</h1>
            <img onClick={clickPencil} className={styles["mainPage__address-edite-pencil"]} src={pencil} alt="редактировать"/>
            <div>
                <input className={styles["mainPage__input-menu"]} placeholder="🔍Введите название блюда" ref={inputRef} onChange={saveValueInput} />
                {visibleInputWindow && <WindowRecherche data={data} close={close} AddInCartFromInputWindow={AddInCartFromInputWindow}/>}
            </div>
            <div className={styles["mainPage__container-registration_flex"]}>
                <img className={styles["mainPage__img_userCircle"]} src={userCircle} alt="иконка пользователя"/>
                <button className={(props.dataButtonLogin.name === "Войти") ? styles["mainPage__button-registration"]: styles["mainPage__button-registration-active"]} onClick={onClickRegistration}>{props.dataButtonLogin.name + " " + props.dataButtonLogin.surname}</button>
            </div>
            <button className={styles["mainPage__button-info"]} onClick={onClickInfo}>Информация</button>
            <div onClick={burgerMenuClick} className={styles["mainPage__container-burgerMenu"]}>
                <img className={styles["mainPage__img-burgerMenu"]} src={MenuBurger} alt="бургер меню"/>
            </div>
            {visibleBurgerMenuWindow && <BurgerMenuWindow onClose={()=>setVisibleBurgerMenuWindow(false)} clickRegistration={onClickRegistration} clickRestaurant={clickAddress} clickInfo={onClickInfo}/>}
            <img className={styles["mainPage__img_cart"]} onClick={cartClick} src={cart} alt="корзина"/>
            {props.countProductCart != undefined && <div className={styles["mainPage__container-count-product"]}><h6 className={styles["mainPage__count-product"]}>{props.countProductCart.length}</h6></div>}
        </div>
        <div className={styles["mainPage__menu_line"]}></div>
        <div className={styles["mainPage__menu-categories"]}>
            <div className={styles["mainPage__container_flex"]}>
                <div className={styles["mainPage__menu-categories-block1"]}>
                    <h2 className={pages[0] ? `${styles["mainPage__menu_h2"]} ${styles["mainPage__line_active"]}` : styles["mainPage__menu_h2"]} onClick={() => handleClick(0)}>Новинки</h2>
                    <h2 className={pages[1] ? `${styles["mainPage__menu_h2"]} ${styles["mainPage__line_active"]}` : styles["mainPage__menu_h2"]} onClick={() => handleClick(1)}>Бургеры</h2>
                    <h2 className={pages[2] ? `${styles["mainPage__menu_h2"]} ${styles["mainPage__line_active"]}` : styles["mainPage__menu_h2"]} onClick={() => handleClick(2)}>Роллы</h2>
                    <h2 className={pages[3] ? `${styles["mainPage__menu_h2"]} ${styles["mainPage__line_active"]}` : styles["mainPage__menu_h2"]} onClick={() => handleClick(3)}>Картошка</h2>
                </div>
                <div className={styles["mainPage__container-more-categories"]}>
                    <h2 className={styles["mainPage__h2-more-categories"]} onClick={clickMoreCategories}>{stateTitle}</h2>
                </div>
            </div>
            <div id="categories-block2" className={styles["mainPage__menu-categories-block2"]}>
                <h2 className={pages[4] ? `${styles["mainPage__menu_h2"]} ${styles["mainPage__line_active"]}` : styles["mainPage__menu_h2"]} onClick={() => handleClick(4)}>Соусы</h2>
                <h2 className={pages[5] ? `${styles["mainPage__menu_h2"]} ${styles["mainPage__line_active"]}` : styles["mainPage__menu_h2"]} onClick={() => handleClick(5)}>Напитки</h2>
                <h2 className={pages[6] ? `${styles["mainPage__menu_h2"]} ${styles["mainPage__line_active"]}` : styles["mainPage__menu_h2"]} onClick={() => handleClick(6)}>Десерты</h2>
                <h2 className={pages[7] ? `${styles["mainPage__menu_h2"]} ${styles["mainPage__line_active"]}` : styles["mainPage__menu_h2"]} onClick={() => handleClick(7)}>Салаты</h2>
            </div>
        </div> 
        <hr></hr>
        <div className={styles["mainPage__wrapper-menu"]}>
            {pages[0] && dataNewMenu.map(e=> <NewMenu img={e.img} title={e.title} price={e.price} masses={e.masses} kj1={e.kj1} calories1={e.calories1} fats1={e.fats1} proteins1={e.proteins1} carbohydrates1={e.carbohydrates1} kj2={e.kj2} calories2={e.calories2} fats2={e.fats2} proteins2={e.proteins2} carbohydrates2={e.carbohydrates2} key={e.title} onClick={addProductCard}/>)}
            {pages[1] && dataBurgerMenu.map(e=> <BurgerMenu img={e.img} title={e.title} price={e.price} masses={e.masses} kj1={e.kj1} calories1={e.calories1} fats1={e.fats1} proteins1={e.proteins1} carbohydrates1={e.carbohydrates1} kj2={e.kj2} calories2={e.calories2} fats2={e.fats2} proteins2={e.proteins2} carbohydrates2={e.carbohydrates2} key={e.title} onClick={addProductCard}/>)}
            {pages[2] && dataRollMenu.map(e=> <RollMenu img={e.img} title={e.title} price={e.price} masses={e.masses} kj1={e.kj1} calories1={e.calories1} fats1={e.fats1} proteins1={e.proteins1} carbohydrates1={e.carbohydrates1} kj2={e.kj2} calories2={e.calories2} fats2={e.fats2} proteins2={e.proteins2} carbohydrates2={e.carbohydrates2} key={e.title} onClick={addProductCard}/>)}
            {pages[3] && dataPotatoMenu.map(e=> <PotatoMenu img={e.img} title={e.title} price={e.price} masses={e.masses} kj1={e.kj1} calories1={e.calories1} fats1={e.fats1} proteins1={e.proteins1} carbohydrates1={e.carbohydrates1} kj2={e.kj2} calories2={e.calories2} fats2={e.fats2} proteins2={e.proteins2} carbohydrates2={e.carbohydrates2} key={e.title} onClick={addProductCard}/>)}
            {pages[4] && dataSaucesMenu.map(e=> <SaucesMenu img={e.img} title={e.title} price={e.price} masses={e.masses} kj1={e.kj1} calories1={e.calories1} fats1={e.fats1} proteins1={e.proteins1} carbohydrates1={e.carbohydrates1} kj2={e.kj2} calories2={e.calories2} fats2={e.fats2} proteins2={e.proteins2} carbohydrates2={e.carbohydrates2} key={e.title} onClick={addProductCard}/>)}
            {pages[5] && dataBeveragesMenu.map(e=> <BeveragesMenu img={e.img} title={e.title} price={e.price} masses={e.masses} kj1={e.kj1} calories1={e.calories1} fats1={e.fats1} proteins1={e.proteins1} carbohydrates1={e.carbohydrates1} kj2={e.kj2} calories2={e.calories2} fats2={e.fats2} proteins2={e.proteins2} carbohydrates2={e.carbohydrates2} key={e.title} onClick={addProductCard}/>)}
            {pages[6] && dataDessertMenu.map(e=> <DessertMenu img={e.img} title={e.title} price={e.price} masses={e.masses} kj1={e.kj1} calories1={e.calories1} fats1={e.fats1} proteins1={e.proteins1} carbohydrates1={e.carbohydrates1} kj2={e.kj2} calories2={e.calories2} fats2={e.fats2} proteins2={e.proteins2} carbohydrates2={e.carbohydrates2} key={e.title} onClick={addProductCard}/>)}
            {pages[7] && dataSaladsMenu.map(e=> <SaladsMenu img={e.img} title={e.title} price={e.price} masses={e.masses} kj1={e.kj1} calories1={e.calories1} fats1={e.fats1} proteins1={e.proteins1} carbohydrates1={e.carbohydrates1} kj2={e.kj2} calories2={e.calories2} fats2={e.fats2} proteins2={e.proteins2} carbohydrates2={e.carbohydrates2} key={e.title} onClick={addProductCard}/>)}
        </div>
        <div className={styles["mainPage__line"]}></div>
        {visibleAddInCart && <AddInCart onClickBlur={onClickBlur} dataProduct={dataCard} FullProductData={FullProductData} validAddSum={()=>props.validAddSum()}/>}
    </div>
    )
}
export default MainPage