import { useState } from "react"
import "./App.css"
import ModulStart from "./components/StartWindow/ModulStart"
import RegistrationWindow from "./components/Registration/RegistrationWindow"
import RestaurantBlock from "./components/Restaurant/RestaurantBlock"
import MainPage from "./components/MainPage/MainPage"
import Cart from "./components/MainPage/Cart"
import AddressData from "./components/Data/AddressData" //* данные адресов *//
import Info from "./components/Info/Info"
 
let b = []
function App() {
  const [registrationWindowVisible, setRegistrationWindowVisible] = useState(false)
  const [restaurantWindowVisible, setRestaurantWindowVisible] = useState(false)
  const [addressDataProps, setAddressDataProps] = useState(AddressData)
  const [dataClickCardButton, setDataClickCardButton] = useState({title: "Выберите адрес"})
  const [kosyk, setKosyk] = useState() //костыль для перерендеринга
  const [newRegistrationData, setNewRegistrationData] = useState({name: "Войти", surname: ""})
  const [closeMainPage, setCloseMainPage] = useState(true)
  const [visibleCart, setVisibleCart] = useState(false) // корзина
  const [visibleInfo, setVisibleInfo] = useState(false)
  const [addressPay, setAddressPay] = useState(undefined)

  const [dataProductCart, setDataProductCart] = useState() // массив объектов заказанных товаров

  const [modulWindowVisible, setModulWindowVisible] = useState(true) // startWindow видимый
  const [flagWindowStart, setFlagWindowStart] = useState(false)
  const [flagValidAddSum, setFlagValidAddSum] = useState(false)

  const visibleRegistration = () => {
    setRegistrationWindowVisible(true)
    setRestaurantWindowVisible(false)
  }
  const visibleRestaurant = () => {
    setRestaurantWindowVisible(true)
    setRegistrationWindowVisible(false)
  }
  const registrationData = (e) => {
    setNewRegistrationData(e)
    setAddressPay(`ул ${e.street}, дом ${e.house}, этаж ${e.floor}, кв ${e.apartment}`)
  }

  
  const addressData = (e)=>{
    setAddressDataProps(e)
  }
  const addressClick = (el)=>{
    AddressData.map(e =>{
      e.visibleClick = false
      if(e.title == el.title){
        e.visibleClick = true
        setKosyk(Math.random())
      } 
    })
  }
  const dataClickCard = (e)=>{
    setRestaurantWindowVisible(false)
    setDataClickCardButton(e)
    setAddressPay(e.title)
    if(flagWindowStart === true){
      setVisibleCart(true)
    }
  }
  const visibleRestaurantClick = ()=>{
    setRestaurantWindowVisible(true)
  }
  const onClickCross = () => {
    setRestaurantWindowVisible(false)
    if(flagWindowStart === true){
      setVisibleCart(true)
    }
  }
  const onClickRegistration = () => {
    setRegistrationWindowVisible(true)
  }
  const closeRegistrationBlock = ()=>{
    setRegistrationWindowVisible(false)
  }
  const onCloseMainPage = () => {
    setCloseMainPage(false)
    setVisibleCart(true)
  }
  
  const FullProductData = (e) => {
    b.push(e)
    setDataProductCart(b)
  }
  const exitCart = () => {
    setVisibleCart(false)
    setCloseMainPage(true)
  }
  const noVisibleCart = () => {
    setDataProductCart()
    b = []
  }
  const visibleInfoCloseMainPage = ()=>{
    setVisibleInfo(true)
    setCloseMainPage(false)
  }
  const exit = () => {
    setVisibleInfo(false)
    setCloseMainPage(true)
  }
  const noVisibleStartWindow = () => {
    setModulWindowVisible(false)
  }
  const clickVisibleStartWindow = () => {
    setModulWindowVisible(true)
  }
  const noVisibleCartPayWindow = () => {
    setVisibleCart(false)
  }
  const validAddSum = ()=> {
    setFlagValidAddSum(true)
  }
  const noValidAddSum = () => {
    setFlagValidAddSum(false)
  }
  return (
    <div className="App">
      {(registrationWindowVisible == false && restaurantWindowVisible == false && closeMainPage) && <MainPage dataClickCardButton={dataClickCardButton} visibleRestaurantClick={visibleRestaurantClick} onClickRegistration={onClickRegistration} dataButtonLogin={newRegistrationData} onCloseMainPage={onCloseMainPage} FullProductData={FullProductData} countProductCart={dataProductCart} visibleInfo={visibleInfoCloseMainPage} visibleCartWindowPay={clickVisibleStartWindow} address={addressPay} flag={() =>setFlagWindowStart(true)} validAddSum={validAddSum}/>}
      <ModulStart visibleRegistration={visibleRegistration} visibleRestaurant={visibleRestaurant} visibleStartWindow={modulWindowVisible} noVisibleStartWindow={noVisibleStartWindow} flag={flagWindowStart} noVisibleCartPayWindow={noVisibleCartPayWindow}/>
      {registrationWindowVisible && <RegistrationWindow registrationData={registrationData} closeRegistrationBlock={closeRegistrationBlock}/>}
      {restaurantWindowVisible && <RestaurantBlock addressData={addressDataProps} addressClick={addressClick} dataClickCard={dataClickCard} onClickCross={onClickCross}/>}
      {visibleCart && <Cart data={dataProductCart} exitCart={exitCart} noVisibleCart={noVisibleCart} address={addressPay} visibleStartWindow={() => clickVisibleStartWindow()} validAddSum={flagValidAddSum} noValidAddSum={noValidAddSum}/>}
      {visibleInfo && <Info exit={exit}/>}
    </div>
  )
}

export default App
