import { useState, useEffect } from "react"
import styles from "./Cart.module.css"
import CardCart from "./CardCart"
import PayWindow from "./PayWindow"
import ExitArrow from "../Generic/ExitArrow"
import Cross from "../Generic/Cross"
 
let sum = []
const Cart = (props) => {
  const [visiblePayWindow, setVisiblePayWindow] = useState(false)
  
  if (props.data !== undefined && props.validAddSum) {
    sum=[]
    props.noValidAddSum()
    props.data.map((e) =>
        sum.push(
        Number(e.price.substring(0, e.price.length - 1)) * Number(e.count)
      )
    )
  }
  const exitCart = () => {
    props.exitCart()
  }
  

  const onClickPay = () => {
    if(props.address !== undefined){
      setVisiblePayWindow(true)
      props.noVisibleCart()
    }
    else{
      props.visibleStartWindow()
    }
  }
  const closeWindow = () => { 
    setVisiblePayWindow(false)
  }
  return (
    <div className={styles["Cart__wrapper"]}>
      <ExitArrow exit={exitCart} />
      <Cross close={exitCart}/>
      {props.count === undefined && props.data === undefined ? (
        <div className={styles["Cart__wrapper-h1"]}><h1 className={styles["Cart__h1"]}>Ваша корзина пустая!</h1></div>
      ) : (
        <div className={styles["Cart__wrapper-card"]}>
          <div className={styles["Cart__wrapper-title_flex"]}>
            <div className={styles["Cart__wrapper1"]}>
              <h3 className={`${styles["Cart__wrapper0"]} ${styles["Cart__h3"]}`}>Ваш заказ</h3>
            </div>
            <h3 className={`${styles["Cart__wrapper2"]} ${styles["Cart__h3"]}`}>Количество</h3>
            <h3 className={`${styles["Cart__wrapper3"]} ${styles["Cart__h3"]}`}>Сумма</h3>
          </div>
          <div className={styles["Cart__wrapper-card-card"]}>
            {props.data.map((e) => (
              <CardCart
                count={e.count}
                data={e.title}
                price={e.price}
                img={e.img} 
                key={Math.random()}
              />
            ))}
          </div>
          <div className={styles["Cart__wrapper_flex"]}>
            <div className={styles["Cart__wrapper4"]}>
              <h3 className={styles["Cart__h3"]}>Общая стоимость:</h3>
            </div>
            <div className={styles["Cart__wrapper5"]}>
              <h3 className={styles["Cart__h3"]}>{sum.reduce((sum, a) => sum + a, 0)}₽</h3>
            </div>
          </div>
          <div className={styles["Cart__wrapper-button"]}>
            <button className={styles["Cart__button"]} onClick={onClickPay}>
              Оплатить
            </button>
          </div>
        </div>
      )}
      {visiblePayWindow && <PayWindow closeWindow={closeWindow} address={props.address} />}
    </div>
  )
}
export default Cart
