import styles from "./PayWindow.module.css"
import Cross from "../Generic/Cross"
import checkmark from "../../img/checkmark.svg" 
import payCart from "../../img/payCard.svg"
const PayWindow = (props) => {
    const onClickBlur = () => { 
        props.closeWindow()
    }
    return (
        <>
            <div className={styles["PayWindow__blur"]} onClick={onClickBlur}></div>
            <div className={styles["PayWindow__container-wrapper-widow"]}>
                <div className={styles["PayWindow__wrapper-widow"]}>
                    <Cross close={() => props.closeWindow()}/>
                    <div className={styles["PayWindow__wrapper-h3"]}><h3 className={styles["PayWindow__h3"]}>Адрес: {props.address}</h3></div>
                    <div className={styles["PayWindow__wrapper-img-payCard"]}>
                        <img className={styles["PayWindow__img-payCard"]} src={payCart} alt=""/>
                    </div>
                    <div className={styles["PayWindow__container_flex"]}>
                        <div className={styles["PayWindow__wrapper-img-checkmark"]}><img className={styles["PayWindow__img-checkmark"]} src={checkmark}/></div>
                        <h1 className={styles["PayWindow__h1"]}>Ваш заказ успешно оплачен</h1>
                    </div>
                </div>
            </div>
        </>
    )
}
export default PayWindow