import arrow from "../../img/arrow.svg"
const ExitArrow = (props) => {
    const exit = ()=>{
        props.exit()
    }
    return (
        <div className={"Container__wrapper-arrow"} onClick={exit}>
            <img src={arrow} className={"Container__img-arrow"}/>
            <h3 className={"Container__h3-arrow"}>Вернуться на главную</h3>
        </div>
    )
}
export default ExitArrow