import styles from "./RestaurantAddressCard.module.css"
import checktick from "../../img/check-tick.svg" 


const RestaurantAddressCard = (props) => {
  const clickCard = ()=> {
    props.addressClick(props) 
  }
  return (
    <div className={props.visibleClick ? `${styles["RestaurantAddressCard__wrapper-card_click"]} ${styles["RestaurantAddressCard__wrapper-card"]}` : styles["RestaurantAddressCard__wrapper-card"]} onClick={clickCard}>
      <div className={styles["RestaurantAddressCard__wrapper"]}>
        <div className={styles["RestaurantAddressCard__wrapper-text-block"]}>
          <h2 className={styles["RestaurantAddressCard__h2"]}>{props.title}</h2>
          <div className={styles["RestaurantAddressCard__wrapper-flex"]}>
            <div className={`${styles["RestaurantAddressCard__wrapper-flex-p"]} ${styles["RestaurantAddressCard__wrapper-flex-p1"]}`}>
              <p className={styles["RestaurantAddressCard__p"]}>телефон:</p> 
              <p className={styles["RestaurantAddressCard__p"]}>+7(000)000-00-00 доб.:{props.number}</p>
            </div>
            <div className={`${styles["RestaurantAddressCard__wrapper-flex-p"]} ${styles["RestaurantAddressCard__wrapper-flex-p2"]}`}>
              <p className={styles["RestaurantAddressCard__p"]}>часы работы:</p>
              <p className={`${styles["RestaurantAddressCard__p"]} ${styles["RestaurantAddressCard__p-green-color"]}`}>Открыт до {props.time}</p>
            </div>
          </div>
        </div>
        
      </div>
      <div className={styles["RestaurantAddressCard__wrapper-img"]}>
          {props.visibleClick && <img src={checktick} className={styles["RestaurantAddressCard__img"]}/>}
          {!props.visibleClick && <div className={styles["RestaurantAddressCard__spaceBlock"]}></div>}
        </div>
    </div>
  )
}
export default RestaurantAddressCard
