import styles from "./BeveragesMenu.module.css"

const BeveragesMenu = (props) => {
  const onClick = () => {
    props.onClick(props)
  } 
  return (
    <div className={styles["BeveragesMenu__wrapper-card_flex"]}>
      <div className={styles["BeveragesMenu__wrapper-card"]}>
        <div className={styles["BeveragesMenu__container-img"]}>{props.img}</div>
        <h2 className={styles["BeveragesMenu__container-title"]}>{props.title}</h2>
        <div className={styles["BeveragesMenu__container_flex"]}>
          <div className={styles["BeveragesMenu__wrapper-button"]}><button className={styles["BeveragesMenu__button"]} onClick={onClick}>заказать</button></div>
          <div><h2 className={styles["BeveragesMenu__h2-color-green"]}>{props.price}</h2></div>
        </div>
      </div>
    </div>
  )
} 
export default BeveragesMenu
