import styles from "./DessertMenu.module.css"

const DessertMenu = (props) => {
  const onClick = () => {
    props.onClick(props)
  } 
  return (
    <div className={styles["DessertMenu__wrapper-card_flex"]}>
      <div className={styles["DessertMenu__wrapper-card"]}>
        <div className={styles["DessertMenu__container-img"]}>{props.img}</div>
        <h2 className={styles["DessertMenu__container-title"]}>{props.title}</h2>
        <div className={styles["DessertMenu__container_flex"]}>
          <div className={styles["DessertMenu__wrapper-button"]}><button className={styles["DessertMenu__button"]} onClick={onClick}>заказать</button></div>
          <div><h2 className={styles["DessertMenu__h2-color-green"]}>{props.price}</h2></div>
        </div>
      </div>
    </div>
  )
}
export default DessertMenu
