import styles from "./CardInput.module.css"
const CardInput = (props)=>{
    const clickCard = () => {
        props.AddInCartFromInputWindow(props)
        props.close()
    }
    return (
        <> 
            <div className={styles["CardInput__flex-container"]} onClick={clickCard}>
                <div className={styles["CardInput__container-Img"]}>{props.img}</div>
                <div className={styles["CardInput__container"]}>
                    <h3 className={styles["CardInput__container-title"]}>{props.title}</h3>
                    <h3 className={styles["CardInput__container-price"]}>{props.price}</h3>
                </div>

            </div>
            <hr></hr>
        </>
    )
}
export default CardInput