import CardInput from "./CardInput"
import styles from "./WindowRecherche.module.css"

const WindowRecherche = (props) => {

    const close = () => {
        props.close()
        
    }
    return (
        <>  
            <div className={styles["WindowRecherche__blur"]} onClick={close}></div>
            <div className={styles["WindowRecherche__container-input"]}>
                {props.data.map(e => <CardInput img={e.img} title={e.title} price={e.price} masses={e.masses} kj1={e.kj1} calories1={e.calories1} fats1={e.fats1} proteins1={e.proteins1} carbohydrates1={e.carbohydrates1} kj2={e.kj2} calories2={e.calories2} fats2={e.fats2} proteins2={e.proteins2} carbohydrates2={e.carbohydrates2} key={e.title} AddInCartFromInputWindow={(e)=> props.AddInCartFromInputWindow(e)} close={close}/>)}
            </div>
        </>
    )
}
export default WindowRecherche