import styles from "./AddInCart.module.css"
import { useState } from "react"
import cross from "../../img/cross.svg"
import plus from "../../img/plus.svg"
import minus from "../../img/minus.svg"
import downArrow from "../../img/downArrow.svg";
import upArrow from "../../img/upArrow.svg"


let count = 1
const AddInCart = (props) => {
  const [countProduct, setCountProduct] = useState(1)
  const [visibleTextCalories, setVisibleTextCalories] = useState(false)
  const [alternativeArrow, setAlternativeArrow] = useState(true)
  const [alternativeButton, setAlternativeButton] = useState(true)

  
  const onClickBlur = () => {
    props.onClickBlur()
  }
  const onClickMinus = () => {
    if(countProduct > 1){
      setCountProduct(countProduct - 1)
    }
  }
  const onClickPlus = () => {
    if(countProduct < 99){
      setCountProduct(countProduct + 1)
    }
  }
  const onClickDownArrow = () => {
    const elementScroll = document.getElementById("elementScroll");
    elementScroll.style.flexDirection = "column";
    setVisibleTextCalories(true)
    setAlternativeArrow(false)
  }
  const onClickUpArrow = () => {
    const elementScroll = document.getElementById("elementScroll");
    elementScroll.style.flexDirection = "row";
    setVisibleTextCalories(false)
    setAlternativeArrow(true)
  }
  const onClickButtonMasses100 = () => {
    setAlternativeButton(true)
  }
  const onClickButtonMassesFull = () => {
    setAlternativeButton(false)
  }
  const addProduct = () => {
    props.validAddSum()
    const newData = {...props.dataProduct}
    newData.count = countProduct
    localStorage.setItem(count, JSON.stringify(newData))
    let a = (JSON.parse(localStorage.getItem(count)))
    count ++
    props.FullProductData(a)
    localStorage.clear()
    onClickBlur()
  }
  const calories = 
    <div>
      <div className={styles["AddInCart__wrapper-button-masses"]}>
        <div><button onClick={onClickButtonMasses100} className={alternativeButton ? `${styles["AddInCart__button-masses"]} ${styles["AddInCart__button-masses-active"]}`: styles["AddInCart__button-masses"]}>100г</button></div>
        <div><button onClick={onClickButtonMassesFull} className={!alternativeButton ? `${styles["AddInCart__button-masses"]} ${styles["AddInCart__button-masses-active"]}`: styles["AddInCart__button-masses"]}>{props.dataProduct.masses}</button></div>
      </div>
      <div className={styles["AddInCart__wrapper_flex"]}>
        <div className={styles["AddInCart__wrapper-item"]}>
          <p className={styles["AddInCart__title-calories"]}>кДж</p>
          <p className={styles["AddInCart__title-calories"]}>{alternativeButton ? props.dataProduct.kj1 : props.dataProduct.kj2}</p>
        </div>
        <div className={styles["AddInCart__wrapper-item"]}>
          <p className={styles["AddInCart__title-calories"]}>кКал</p>
          <p className={styles["AddInCart__title-calories"]}>{alternativeButton ? props.dataProduct.calories1 : props.dataProduct.calories2}</p>
        </div>
        <div className={styles["AddInCart__wrapper-item"]}>
          <p className={styles["AddInCart__title-calories"]}>Жиры</p>
          <p className={styles["AddInCart__title-calories"]}>{alternativeButton ? props.dataProduct.fats1 : props.dataProduct.fats2}</p>
        </div>
        <div className={styles["AddInCart__wrapper-item"]}>
          <p className={styles["AddInCart__title-calories"]}>Белки</p>
          <p className={styles["AddInCart__title-calories"]}>{alternativeButton ? props.dataProduct.proteins1 : props.dataProduct.proteins2}</p>
        </div>
        <div className={styles["AddInCart__wrapper-item"]}>
          <p className={styles["AddInCart__title-calories"]}>Углеводы</p>
          <p className={styles["AddInCart__title-calories"]}>{alternativeButton ? props.dataProduct.carbohydrates1 : props.dataProduct.carbohydrates2}</p>
        </div>
      </div>
    </div>
  return (
    <>
      <div className={styles["AddInCart__blur"]} onClick={onClickBlur}></div>
      <div className={styles["AddInCart__container-wrapper-window"]}>
        <div className={styles["AddInCart__wrapper-window"]}>
          <div className={styles["AddInCart__container-img-cross"]}>
            <img
              className={styles["AddInCart__img-cross"]}
              onClick={onClickBlur}
              src={cross}
              alt=""
            /> 
          </div>
          <h2 className={styles["AddInCart__h2-title"]}>
            {props.dataProduct.title}
          </h2>
          <div className={styles["AddInCart__wrapper-h3-calories"]}><h3 className={styles["AddInCart__h3-calories"]}>Пищевая ценность</h3>{alternativeArrow ? <img className={styles["AddInCart__img-Arrow"]} onClick={onClickDownArrow} src={downArrow} alt="стрелка вниз"/> : <img className={styles["AddInCart__img-Arrow"]} onClick={onClickUpArrow} src={upArrow} alt="стрелка вверх"/>}</div>
          <div id="elementScroll" className={styles["AddInCart__wrapper-scroll"]}>
            {visibleTextCalories && calories}
            <div className={styles["AddInCart__wrapper-img"]}>{props.dataProduct.img}</div>
          </div>
          <div className={styles["AddInCart__wrapper-flex"]}>
            <div className={styles["AddInCart__flex-container-button"]}>
              <div className={styles["AddInCart__button-AddInCart"]} onClick={onClickMinus}><img className={styles["AddInCart__img-plus-minus"]} src={minus} alt="минус"/></div>
              <div className={styles["AddInCart__button-AddInCart"]}>{countProduct}</div>
              <div className={styles["AddInCart__button-AddInCart"]} onClick={onClickPlus}><img className={styles["AddInCart__img-plus-minus"]} src={plus} alt="плюс"/></div>
            </div>
            <div className={styles["AddInCart__wrapper_button-Add"]}>
              <button className={styles["AddInCart__button-Add"]} onClick={addProduct}> 
                Добавить в корзину
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}
export default AddInCart
