import styles from "./SaucesMenu.module.css"

const SaucesMenu = (props) => {
  const onClick = () => {
    props.onClick(props)
  }
  return (
    <div className={styles["SaucesMenu__wrapper-card_flex"]}>
      <div className={styles["SaucesMenu__wrapper-card"]}>
        <div className={styles["SaucesMenu__container-img"]}>{props.img}</div>
        <h2 className={styles["SaucesMenu__container-title"]}>{props.title}</h2>
        <div className={styles["SaucesMenu__container_flex"]}>
          <div className={styles["SaucesMenu__wrapper-button"]}><button className={styles["SaucesMenu__button"]} onClick={onClick}>заказать</button></div>
          <div><h2 className={styles["SaucesMenu__h2-color-green"]}>{props.price}</h2></div>
        </div>
      </div>
    </div>
  ) 
}
export default SaucesMenu
