import styles from "./Info.module.css"
import ExitArrow from "../Generic/ExitArrow"
import Cross from "../Generic/Cross"
const Info = (props) => {
  return (
    <div className={styles["Info__wrapper"]}>
      <ExitArrow exit={() => props.exit()} />
      <Cross close={() => props.exit()}/>
      <div className={styles["Info__wrapper-p"]}>
        <p className={styles["Info__p"]}>
          Добро пожаловать на наш сайт по доставке еды в ресторане или прямо к
          вам домой! Мы – команда профессионалов, стремящихся сделать ваш опыт
          вкусно и удобно питаться максимально приятным. Независимо от того,
          хотите ли вы насладиться блюдом в уютной атмосфере нашего ресторана
          или предпочитаете комфорт домашнего пространства, наша услуга доставки
          позволит вам насладиться изысканными блюдами прямо на вашем столе. Наш
          меню представляет широкий ассортимент разнообразных блюд,
          приготовленных с любовью и неизменно свежих ингредиентов. Мы гордимся
          качеством нашей кухни - заказывая у нас, вы можете быть уверены, что
          получите вкусное и сбалансированное блюдо, которое удовлетворит все
          ваши вкусовые предпочтения.
        </p>
      </div>
    </div>
  )
}
export default Info
