import { useState } from "react"
import RegistrationBlock from "./RegistrationBlock"

const RegistrationWindow = (props) => {
  const [registrationVisible, setRegistrationVisible] = useState(true)

  const onClose = () => {
    setRegistrationVisible(false)
  }
  const registrationData = (e) => {
    props.registrationData(e)
  }
  const closeRegistrationBlock = () => {
    props.closeRegistrationBlock()
  }
  return (
    <>
      {registrationVisible && <RegistrationBlock onClose={onClose} registrationData={registrationData} closeRegistrationBlock={closeRegistrationBlock}/>}
    </>
  )
}
export default RegistrationWindow
