import styles from "./CardCart.module.css"

 
const CardCart = (props) => {
    return (
        <div className={styles["CardCart__wrapper-card"]}>
            <div className={styles["CardCart__wrapper-text_flex"]}> 
                <div className={styles["CardCart__wrapper1"]}><h3 className={styles["CardCart__h3"]}>{props.data}</h3></div>
                <div className={styles["CardCart__wrapper2"]}><h3 className={styles["CardCart__h3"]}>{props.count}</h3></div>
                <div className={styles["CardCart__wrapper3"]}><h3 className={styles["CardCart__h3"]}>{Number(props.price.substring(0, props.price.length - 1)) * Number(props.count)+"₽"}</h3></div>
            </div>
        </div>
    )
    
}
export default CardCart